angular
  .module('w69b.chromePermissions', ['w69b.promiseTool'])
  .factory('chromePermissions', function (promiseTool) {
    var permissions = window.chrome && window.chrome.permissions;
    var api = {};
    if (!permissions) return api;

    api.contains = promiseTool.wrapChromeError(
      permissions.contains,
      permissions,
    );
    api.request = promiseTool.wrapChromeError(permissions.request, permissions);
    api.remove = promiseTool.wrapChromeError(permissions.remove, permissions);
    api.getAll = promiseTool.wrapChromeError(permissions.getAll, permissions);

    return api;
  });
