angular
  .module('castifyExt.ui.loadingOverlay', [])
  .directive('cfLoadingBackdrop', function () {
    return {
      restrict: 'E',
      template:
        '<md-progress-circular md-mode="indeterminate"></md-progress-circular>',
    };
  })
  .directive('cfLoadingOverlay', function ($compile) {
    return {
      restrict: 'A',
      link: function (scope, elem, attrs) {
        var shownEl = null;
        scope.$watch(attrs.cfLoadingOverlay, function (show) {
          if (show) {
            shownEl = $compile('<cf-loading-backdrop></cf-loading-backdrop>')(
              scope,
            );
            elem.append(shownEl);
          } else if (shownEl) {
            shownEl.remove();
            shownEl = null;
          }
        });
      },
    };
  });
