angular.module('w69b.spawn', []).factory('spawn', function ($q) {
  function spawn(generatorFunc, useNative) {
    var reject = useNative ? Promise.reject.bind(Promise) : $q.reject.bind($q);
    var when = useNative ? Promise.resolve.bind(Promise) : $q.when.bind($q);

    function continuer(fn, arg) {
      var result;
      try {
        result = fn(arg);
      } catch (err) {
        return reject(err);
      }
      if (result.done) {
        return when(result.value);
      } else {
        return when(result.value).then(next, throwError);
      }
    }

    var generator = generatorFunc();
    var next = continuer.bind(null, generator.next.bind(generator));
    var throwError = continuer.bind(null, generator.throw.bind(generator));
    return next();
  }

  spawn.wrap = function (fn) {
    return function () {
      var args = arguments;
      return spawn(fn.bind(this, ...args));
    };
  };

  return spawn;
});
