angular
  .module('w69b.chromeRuntime', ['w69b.promiseTool'])
  .factory('chromeRuntime', [
    'promiseTool',
    function (promiseTool) {
      var api = {};

      api.sendMessage = promiseTool.wrapChromeError(
        chrome.runtime.sendMessage,
        chrome.runtime,
      );

      api.getBackgroundPage = promiseTool.wrapChromeError(
        chrome.runtime.getBackgroundPage,
        chrome.runtime,
      );

      api.connect = chrome.runtime.connect.bind(chrome.runtime);
      api.getManifest = chrome.runtime.getManifest.bind(chrome.runtime);
      api.id = chrome.runtime.id;

      if (chrome.runtime.getPlatformInfo) {
        api.getPlatformInfo = promiseTool.wrapChromeError(
          chrome.runtime.getPlatformInfo,
          chrome.runtime,
        );
      }

      api.setUninstallURL = promiseTool.wrapChromeError(
        chrome.runtime.setUninstallURL,
        chrome.runtime,
      );

      // forward event handlers
      [
        'onMessage',
        'onMessageExternal',
        'onConnect',
        'onConnectExternal',
        'onInstalled',
        'onSuspend',
      ].forEach(function (event) {
        api[event] = {
          addListener: function (listener) {
            chrome.runtime[event].addListener(listener);
          },
          removeListener: function (listener) {
            chrome.runtime[event].removeListener(listener);
          },
        };
      });

      return api;
    },
  ]);
