angular
  .module('castifyExt.backgroundTool', ['w69b.chromeRuntime'])
  .factory('backgroundTool', [
    'chromeRuntime',
    '$q',
    '$rootScope',
    function (chromeRuntime, $q, $rootScope) {
      var loadPromise;

      /**
       * Loads background page like chrome.runtime.getBackgroundPage but waits until async
       * initialization of the background page has completed before resolving.
       */
      function loadBackground() {
        var deferred = $q.defer();
        chromeRuntime.onMessage.addListener(function onMessage(msg) {
          if (msg !== 'bg:loaded') return;
          chromeRuntime.onMessage.removeListener(onMessage);
          deferred.resolve();
        });
        chromeRuntime.sendMessage('bg:load').catch(angular.identity);
        return deferred.promise;
      }

      var api = {};

      api.portHelper = function (name, waitForFullLoad) {
        var port;
        var helper = {};
        /**
         * @return {Promise} promise that resolves to port when background is loaded and
         * port is connected. The port is cached within this helper.
         */
        helper.getPort = function () {
          var loadPromise =
            waitForFullLoad === false ? $q.when() : loadBackground();
          return loadPromise.then(function () {
            if (!port) {
              port = chromeRuntime.connect({ name: name });
            }
            return port;
          });
        };

        /**
         * Post message in type/data format to cached port.
         * @param {string} type command that is passed Ex: stop, start, pause, resume
         * @param {Object} data recording configuration object
         */
        helper.postMessage = function (type, data) {
          return helper.getPort().then(function (port) {
            return port.postMessage({ type: type, data: data });
          });
        };

        /**
         * @param {Object} msgHandlers object mapping message type to a handler function.
         * @return {function()} function that removes message handlers from port.
         */
        helper.installHandlers = function (msgHandlers) {
          function listener(msg) {
            if (msgHandlers.hasOwnProperty(msg.type))
              msgHandlers[msg.type](msg.data);
            if (!$rootScope.$$phase) $rootScope.$apply();
          }

          function unlisten() {
            helper.getPort().then(function (port) {
              port.onMessage.removeListener(listener);
            });
          }

          helper.getPort().then(function (port) {
            port.onMessage.addListener(listener);
          });
          return unlisten;
        };

        return helper;
      };

      /**
       * Function that loads the background page and returns a Promise that
       * resolves when loaded.
       */
      api.loadBackground = function () {
        if (!loadPromise) loadPromise = loadBackground();
        return loadPromise;
      };

      return api;
    },
  ]);
