angular
  .module('w69b.promiseTool', [])
  .factory('promiseTool', function ($q, $timeout) {
    var api = {};

    /**
     * Calls method(args..., callback) and wraps
     * result in a promise. It assumes that chrmoe.runtime.lastError is set
     * in the callback if there was an error and rejects with this error.
     * @param {function} fn function to call.
     * @param {Object} thisobj this object.
     * @return {function():Promise} with wrapped result.
     */
    api.wrapChromeError = function (fn, thisobj) {
      return function () {
        var args = Array.prototype.slice.call(arguments, 0);
        var deferred = $q.defer();
        args.push(function (result) {
          if (chrome.runtime && chrome.runtime.lastError)
            deferred.reject(chrome.runtime.lastError);
          else deferred.resolve(result);
        });
        fn.apply(thisobj, args);
        return deferred.promise;
      };
    };

    /**
     * Calls method(args..., successCallback, errCallback) and wraps
     * result in a promise.
     * @param {function} fn function to call.
     * @param {Object} thisobj this object.
     * @return {function():Promise} with wrapped result.
     */
    api.wrapCallbacks = function (fn, thisobj) {
      return function () {
        var args = Array.prototype.slice.call(arguments, 0);
        var deferred = $q.defer();
        args.push(deferred.resolve.bind(deferred));
        args.push(deferred.reject.bind(deferred));
        fn.apply(thisobj, args);
        return deferred.promise;
      };
    };

    /**
     * Helper to serialize function calls that return promises.
     * @return {function(function())} serialization function.
     */
    api.serializer = function () {
      var serializationPromise = null;

      var serialize = function (fn) {
        var deferred = $q.defer();
        // wait till previous request is complete.
        (serializationPromise || $q.when()).then(function () {
          (fn.call() || $q.when()).then(
            (r) => deferred.resolve(r),
            (err) => deferred.reject(err),
          );
        });
        serializationPromise = deferred.promise.catch(() => null);
        serializationPromise.then(function () {
          serializationPromise = null;
        });
        return deferred.promise;
      };
      serialize.wrap = function (fn) {
        return function () {
          return serialize(fn.bind(this, ...arguments));
        };
      };
      return serialize;
    };

    /**
     * Run fn and wait for result. If it does not resolve/reject after the timeout, reject promise.
     * @param {function()} fn
     * @param {number} timeoutMs timeout in ms.
     * @param {function()=} onTimeout optional callback to execute when timeout occurs.
     * @return {Promise} that resolves/rejects with original result,
     * resolves with the return value of onTimeout() if defined or rejects with 'timeout'
     * (in this order of preference).
     */
    api.withTimeout = function (fn, timeoutMs, onTimeout) {
      if (!onTimeout) onTimeout = angular.noop;
      var deferred = $q.defer();
      var timeoutTimer = $timeout(function timedOut() {
        var result = onTimeout();
        if (angular.isDefined(result)) deferred.resolve(result);
        else deferred.reject('timeout');
        deferred = null;
      }, timeoutMs);
      var result = fn();
      function cancelTimer() {
        $timeout.cancel(timeoutTimer);
        timeoutTimer = null;
      }
      result.then(
        function () {
          cancelTimer();
          if (deferred) deferred.resolve.apply(deferred, arguments);
        },
        function () {
          cancelTimer();
          if (deferred) deferred.reject.apply(deferred, arguments);
        },
      );
      var promise = deferred.promise;
      return promise;
    };

    return api;
  });
