angular
  .module('w69b.timeutil', [])
  .factory('timeutil', function () {
    var api = {};
    /**
     * Formats seconds as human time.
     * @param {number=} seconds number of seconds.
     * @return {string} HH:mm:ss string.
     */
    api.formatDuration = function (seconds) {
      if (!angular.isNumber(seconds)) seconds = 0;
      function pad(num) {
        if (num < 10) return '0' + num;
        else return num;
      }

      var prefix = '';
      if (seconds < 0) {
        prefix = '-';
        // Add 1 - epsilon to get consistent [0, 1) rounding intervals for negative numbers.
        seconds = -seconds + 0.99;
      }

      var sec = Math.floor(seconds % 60);
      var min = Math.floor((seconds / 60) % 60);
      var result = [pad(min), pad(sec)];
      var hours = Math.floor(seconds / 3600);
      if (hours) result.unshift(hours);
      return prefix + result.join(':');
    };

    return api;
  })
  .filter('duration', [
    'timeutil',
    function (timeutil) {
      return function (seconds) {
        return timeutil.formatDuration(seconds || 0);
      };
    },
  ]);
