angular
  .module('castifyExt.ui.themeConfig', ['ngMaterial'])
  .config(function ($mdThemingProvider) {
    $mdThemingProvider.definePalette('castify', {
      50: 'ffffff', // white (background default)
      100: '555555', // dark gray (text default)
      200: 'ef9a9a',
      300: 'e57373', // orange (gradient end)
      400: 'ef5350',
      500: 'ff8282', // DEFAULT pink (gradient start)
      600: 'e53935',
      700: 'd32f2f',
      800: 'c62828',
      900: 'b71c1c',
      A100: 'fd8c00', // orange (links default)
      A200: 'ff8282', // pink (accent for toggles, etc.)
      A400: '00aaff', // blue (buttons)
      A700: 'd50000',
      // 'contrastDefaultColor': 'light', // whether, by default, text (contrast)
      //                                     // on this palette should be dark or light

      // 'contrastDarkColors': ['50', '100', // hues which contrast should be 'dark' by default
      //  '200', '300', '400', 'A100'],
    });

    $mdThemingProvider
      .theme('default')
      .primaryPalette('castify', {
        default: '500',
        'hue-1': 'A100',
        'hue-2': 'A200',
        'hue-3': 'A400',
      })
      .accentPalette('pink');
  })
  .config(function ($mdIconProvider) {
    $mdIconProvider.defaultIconSet('/assets/images/old_ajs_icons.svg');
  });
