angular
  .module('w69b.webrtc', ['w69b.promiseTool', 'w69b.spawn'])
  .factory('webrtc', function (promiseTool, $window, $q, spawn, $log) {
    var api = {};

    var nav = $window.navigator;

    api.getUserMedia = promiseTool.wrapCallbacks(
      nav.getUserMedia ||
        nav.webkitGetUserMedia ||
        nav.mozGetUserMedia ||
        nav.msGetUserMedia,
      nav,
    );

    if (nav.mediaDevices && nav.mediaDevices.enumerateDevices) {
      api.getSources = function () {
        return $q
          .when(nav.mediaDevices.enumerateDevices())
          .then(function (srcs) {
            return srcs
              .filter(function (src) {
                return src.kind.endsWith('input');
              })
              .map(function (dev) {
                var src = {
                  id: dev.deviceId,
                  label: dev.label,
                };
                src.kind = dev.kind.substring(0, dev.kind.length - 5);
                return src;
              });
          })
          .catch((err) => {
            // Treat failure to enumerate devices as if no devices is present.
            // See https://trello.com/c/znNtF0qn for details.
            $log.info(
              'webrtc: enumarateDevices() failed, treating it as no devices present',
              err,
            );
            return [];
          });
      };
    } else {
      api.getSources = promiseTool.wrapCallbacks(
        MediaStreamTrack.getSources,
        MediaStreamTrack,
      );
    }

    api.getSourcesByKind = function (type) {
      return api.getSources().then(function (sources) {
        return sources.filter(function (source) {
          return source.kind === type;
        });
      });
    };

    api.getVideoSources = function () {
      return api.getSourcesByKind('video');
    };

    api.getAudioSources = function () {
      return api.getSourcesByKind('audio');
    };

    api.hasCamera = function () {
      return api.getVideoSources().then(function (cams) {
        return cams.length > 0;
      });
    };

    api.hasMicrophone = function () {
      return api.getAudioSources().then(function (mics) {
        return mics.length > 0;
      });
    };

    function hasLabel(sources) {
      return sources.some(function (source) {
        return !!source.label;
      });
    }

    /**
     * Check if App has video access without triggering a request prompt.
     * We assume that source label is empty if permission has not been granted yet.
     * This is how Chrome handles permissions for secure-origin sites.
     * @returns {Promise} that resolves with true/false.
     */
    api.hasVideoAccess = function () {
      return api.getVideoSources().then(hasLabel);
    };

    /**
     * See hasVideoAccess
     */
    api.hasAudioAccess = function () {
      return api.getAudioSources().then(hasLabel);
    };

    /**
     * Get stream for all device types that are available and immediately
     * stop streams them again.
     * Intended for requesting access on secure-origin pages, where access setting
     * is remembered by the browser between consecutive requests.
     * Returns promise that resolves with object:
     * {audio: hasGrantedAudioAccess, video: hasGrantedVideoAccess}
     */
    api.getCameraAndMicPermission = spawn.wrap(function* () {
      const hasCam = yield api.hasCamera();
      let audio = false;
      let video = false;
      try {
        const stream = yield api.getUserMedia({ audio: true, video: hasCam });
        video = !!stream.getVideoTracks().length;
        audio = !!stream.getAudioTracks().length;
        stream.getTracks().forEach(function (track) {
          track.stop();
        });
      } catch (_) {
        // ignored
      }
      return { video, audio };
    });
    return api;
  });
