angular
  .module('castifyExt.optionalPermissions', ['w69b.chromePermissions'])
  .factory('optionalPermissions', function (chromePermissions) {
    var api = {};
    var TAB_PERMISSIONS = {
      permissions: ['tabCapture', 'webNavigation'],
      origins: ['<all_urls>'],
    };
    var TAB_PERMISSIONS_LITE = {
      permissions: ['tabCapture', 'webNavigation'],
      origins: [],
    };

    TAB_PERMISSIONS.permissions.push('activeTab');
    TAB_PERMISSIONS_LITE.permissions.push('activeTab');

    function getTabPermissions(config) {
      if (config && (!config.draw || (config.draw && !config.draw.showToolbox)))
        return TAB_PERMISSIONS_LITE;
      return TAB_PERMISSIONS;
    }

    api.hasLiteTabPermissions = function () {
      return chromePermissions.contains(TAB_PERMISSIONS_LITE);
    };

    api.hasTabPermissions = function (config) {
      const perm = getTabPermissions(config);
      // HACK, attempted fix for https://trello.com/c/ncPxMgd0
      // contains() always returns false in Chrome 72+, if origin is passed in
      // as well.
      return chromePermissions.contains({ permissions: perm.permissions });
    };

    api.requestTabPermissions = function (config) {
      return chromePermissions.request(getTabPermissions(config));
    };

    return api;
  });
