/**
 * Client to access chrome background-page recording service.
 */
angular
  .module('castifyExt.recorder', [
    'castifyExt.backgroundTool',
    'castifyExt.optionalPermissions',
    'w69b.spawn',
  ])
  .factory(
    'recorder',
    function (backgroundTool, optionalPermissions, spawn, $q) {
      var portHelper = backgroundTool.portHelper('recorder', false);
      var connectedDeferred = $q.defer();
      var api = {};

      /**
       * Current state of the recorder.
       * - config: Object (recording config)
       * - previewShown: boolean (if live preview window is shown)
       * - isBlocked: boolean (true while recorder is busy. For example when a state change is in
       *  progress. it does not react to controls while busy)
       * - state: one of 'paused', 'recording', 'inactive', 'closed'
       * - fps: float (fps estimate)
       * - recordingResolution: {width: number, height: number} (current recording resolution)
       * @type {Object}
       * - time: float (recording time in ms)
       */
      api.info = {};
      api.connectedPromise = connectedDeferred.promise;

      var msgHandlers = {};
      msgHandlers.stateUpdate = function (info) {
        angular.copy(info, api.info);
        // state update is posted on connect
        if (connectedDeferred) {
          connectedDeferred.resolve();
          connectedDeferred = null;
        }
      };

      portHelper.installHandlers(msgHandlers);

      /**
       * Get state as promise
       * @return {Promise} that resolves with state object when connected.
       */
      api.getState = function () {
        return api.connectedPromise.then(function () {
          return api.info;
        });
      };
      /**
       * Start recording.
       * @param {Object} config recording config, see start() in bgRecordService for details.
       */
      api.start = spawn.wrap(function* (config) {
        if (config.captureSource === 'tab') {
          var gotPerm = yield optionalPermissions.requestTabPermissions(config);
          if (!gotPerm) return $q.reject('tab_permissions');
        }
        return portHelper.postMessage('start', config);
      });

      api.stop = function () {
        portHelper.postMessage('stop');
      };

      api.restart = function () {
        portHelper.postMessage('restart');
      };

      api.pause = function () {
        portHelper.postMessage('pause');
      };

      api.resume = function () {
        portHelper.postMessage('resume');
      };

      /**
       * Discard current recording.
       */
      api.discard = function () {
        portHelper.postMessage('discard');
      };

      api.replaceStream = function (config) {
        portHelper.postMessage('replaceStream', config);
      };

      api.cancelPendingStart = function () {
        portHelper.postMessage('cancelPendingStart');
      };

      api.showPreview = function (show) {
        portHelper.postMessage('showPreview', show);
      };

      api.setAutoStop = function (minutes) {
        portHelper.postMessage('setAutoStop', minutes);
      };

      // connect
      portHelper.getPort();
      return api;
    },
  );
