import * as angular from 'angular';
import '../../../w69b/webrtc';
import { StartRecordingConfig } from '../../bg-record';

angular
  .module('castifyExt.ui.livepreview', [
    'w69b.promiseTool',
    'w69b.timeutil',
    'castifyExt.recorder',
    'castifyExt.ui.loadingOverlay',
    'ngMaterial',
    'w69b.webrtc',
  ])
  .directive('cfLivePreviewCanvas', function (recorder, webrtc) {
    return {
      restrict: 'EA',
      template: require('./livePreviewCanvas.html'),
      replace: true,
      scope: {},
      link: function postLink(scope, elem) {
        const videos = elem.find('video');
        const camVideo = videos.eq(0) as any;
        const setVideoToWebcam = async function (config: StartRecordingConfig) {
          camVideo[0].srcObject = await webrtc.getUserMedia({
            video: {
              optional: [{ sourceId: config.camSourceId }],
            },
          });
        };

        camVideo[0].volume = 0;

        camVideo.bind('canplay', function () {
          camVideo.removeClass('ng-hide');
        });

        const deregisterWatcher = scope.$watch(
          function () {
            return recorder.info.config;
          },
          (cfg) => {
            if (!cfg) {
              return;
            }
            deregisterWatcher();
            setVideoToWebcam(cfg);
          },
        );
      },
    };
  })

  .component('cfLivepreview', {
    template: require('./livepreview.html'),
    controller($scope, recorder) {
      $scope.recorder = recorder;
    },
  });
